<template>
  <div class="holder">
    <input type="text"
           class="form-control"
           v-model="input"
           :disabled="disabled"
           :placeholder="placeholder"
           @keydown="keypress($event)"
           ref="inputElement"/>
    <div v-if="!selectionDone && suggest"
         v-on-clickaway="unselect"
         class="typeahead bg-light border border-dark"
         :style="{top: inputFieldHeight}">
      <a v-for="(d, idx) in data"
         :key="d['id']"
         :ref="'dropdown_' + idx"
         class="dropdown-item"
         :class="{'bg-white': selectedSuggestion === d}"
         href="#"
         @mouseover="selectedSuggestion = d"
         @click="selectOption(d)">
        {{ d }}
      </a>
    </div>
  </div>
</template>

<script>
import {mixin as clickaway} from 'vue-clickaway';

export default {
  props: {
    value: String,
    data: Array,
    disabled: Boolean,
    suggest: Boolean,
    placeholder: String
  },
  data() {
    return {
      input: this.value,
      selectionDone: true,
      selectedSuggestion: ''
    }
  },
  computed: {
    inputFieldHeight() {
      let inputElement = this.$refs['inputElement'];
      return new Number(inputElement.offsetTop + inputElement.offsetHeight) + 'px';
    }
  },
  methods: {
    keypress(event) {
      this.selectionDone = false;
      let charCode = event.keyCode || event.which;
      if (charCode == 13) {
        if (this.selectedSuggestion)
          this.selectOption(this.selectedSuggestion)
      } else if (charCode == 40) { // down}
        let idx = this.data.indexOf(this.selectedSuggestion);
        this.selectedSuggestion = this.data[idx + 1];
      }
    },
    selectOption(d) {
      this.input = d;
      this.selectionDone = true;
      this.selectedSuggestion = undefined
    },
    unselect() {
      this.selectionDone = true
    }
  },
  watch: {
    input(newVal) {
      this.$emit('change', newVal);
    },
    value() {
      this.input = this.value;
    }
  },
  mixins: [clickaway]
}
</script>

<style>
.holder {
  position: relative;
}

.typeahead {
  position: absolute;
  left: 1rem;
  z-index: 50;
}
</style>
