<template>
  <div>
    <div class="card"
         v-if="patient">
      <div class="card-header d-flex">
        <div>
          <icon icon="file-alt"
                size="2x"
                class="py-1 text-info align-middle"/>
          Patientenakte
        </div>
        <div class="ml-auto mr-0">
          <a href="#" @click.prevent="$emit('edit', patient)">
            <icon icon="edit"
                  size="1x"
                  class="text-primary"/>
            bearbeiten
          </a>
        </div>
      </div>
      <div class="card-body">
        <div class="card-title h5">
          {{ `${patient.salutation()} ${patient.firstName} ${patient.lastName}` }}
        </div>
        <div class="card-section">
          <div class="card-section-title col-12">Allgemein</div>
          <div class="row">
            <div class="col-3 text-right">
              KVNr:
            </div>
            <div class="col-9">
              {{ patient.insurantNumber }}
            </div>
          </div>
          <div class="row">
            <div class="col-3 text-right">
              Geburtsdatum:
            </div>
            <div class="col-9">
              {{ patient.dateOfBirth | date }}
            </div>
          </div>
        </div>
        <div class="card-section">
          <div class="card-section-title col-12">Adresse</div>
          <div class="row">
            <div class="col-3 text-right">
              Stra√üe:
            </div>
            <div class="col-9">
              {{ patient.street }}
            </div>
          </div>
          <div class="row">
            <div class="col-3 text-right">
              Hausnummer:
            </div>
            <div class="col-9">
              {{ patient.streetNumber }}
            </div>
          </div>
          <div class="row">
            <div class="col-3 text-right">
              Postleitzahl:
            </div>
            <div class="col-9">
              {{ patient.zip }}
            </div>
          </div>
          <div class="row">
            <div class="col-3 text-right">
              Stadt:
            </div>
            <div class="col-9">
              {{ patient.city }}
            </div>
          </div>
        </div>
        <div class="card-section">
          <div class="card-section-title col-12">Kontakt</div>
          <div class="row">
            <div class="col-3 text-right">
              Telefon:
            </div>
            <div class="col-9">
              {{ patient.phone }}
            </div>
          </div>
          <div class="row">
            <div class="col-3 text-right">
              Mobil:
            </div>
            <div class="col-9">
              {{ patient.mobile }}
            </div>
          </div>
          <div class="row">
            <div class="col-3 text-right">
              Email:
            </div>
            <div class="col-9">
              {{ patient.email }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    patient: Object
  }
}
</script>

<style>
</style>

<style lang="scss">
@import '../../assets/styles/custom.scss';

.card-section-title {
  background-color: theme-color-level("info", 0);
  text-align: center;
  color: theme-color("light");
  padding: 0.3em 0 0.3em 0;
}

.card-section {
  margin-top: 1em;
  border: solid 1px theme-color("info");
  padding: 0;
}

</style>
