import store from '@/store'
import Person from '@/models/person'

class PatientService {

    getAll() {
        return store.getters.api.get('persons')
            .then(response => {
                let result = response.data.data.map(p => new Person(p))
                return Promise.resolve(result.sort(Person.compare))
            })
    }

    getAllFiltered(filter) {
        if (!filter || filter.length == 0)
            return this.getAll()
        return this.getAll().then(allPatients => {
            return Promise.resolve(allPatients.filter(p =>
                p.firstName.toLowerCase().startsWith(filter.toLowerCase()) ||
                p.lastName.toLowerCase().startsWith(filter.toLowerCase()))
            )
        })
    }

}

const patientService = new PatientService()

export default patientService
