export default class Person {

    constructor(data) {
        this.id = data.id
        this.firstName = data.firstName
        this.lastName = data.lastName
        this.dateOfBirth = new Date(data.dateOfBirth)
        this.insurantNumber = data.insurantNumber
        this.title = data.title
        this.gender = new Number(data.gender)
        this.street = data.street
        this.streetNumber = data.streetNumber
        this.zip = data.zipcode
        this.city = data.city
        this.phone = data.phone
        this.mobile = data.mobile
        this.email = data.email
        this.healthInsurance = data.healthInsurance
    }

    salutation() {
        return (this.gender == 0 || this.gender == 30) ? ''
            : (this.gender == 10 ? 'Herr' : 'Frau')
    }

    static compare(u1, u2) {
        let res = u1.lastName.localeCompare(u2.lastName)
        if (!res)
            res = u1.firstName.localeCompare(u2.firstName)
        return res
    }
}
