<template>
  <div>
    <div class="d-flex">
      <typeahead-input
          ref="searchInput"
          :data="patients"
          :suggest="false"
          :value="filter"
          placeholder="Filter"
          @change="$emit('filterChange', $event)"/>
      <a href="#"
         @click.prevent="$emit('filterChange', '')">
        <icon icon="times-circle"
              class="pl-2 text-primary pointer-event"
              size="2x"/>
      </a>
    </div>
    <div class="mt-2">
      <ul class="list-group">
        <li v-for="patient in patients"
            :key="patient.id"
            class="list-group-item list-group-item-action py-1 d-inline-flex pl-2"
            :class="{active: selectedPatient === patient}"
            @click="$emit('patientSelected', patient)">
          <div class="h-100">
            <icon icon="user"
                  size="1x"
                  :class="{'text-primary': selectedPatient != patient,
                  'text-light': selectedPatient == patient}"/>
          </div>
          <div class="d-block ml-1">
            {{ `${patient.lastName}, ${patient.firstName}` }}
            <p class="text-secondary small mb-0">{{ patient.dateOfBirth | date }}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import TypeaheadInput from "@/components/common/TypeaheadInput";

export default {
  props: {
    patients: Array,
    selectedPatient: Object,
    filter: String
  },
  components: {
    TypeaheadInput
  }
}
</script>
