<template>
  <div class="container-fluid mx-0 fill">
    <div class="row fill">
      <div class="col-4 pt-2 bg-light">
        <patient-list
            :patients="patients"
            :selected-patient="selectedPatient"
            :filter="filter"
            @patientSelected="selectedPatient = $event"
            @filterChange="filter = $event"/>
      </div>
      <div class="col-8 pt-2">
        <patient-detail
            v-if="!modeEdit"
            :patient="selectedPatient"
            @edit="modeEdit = true"/>
        <patient-editor
            v-if="modeEdit"
            :patient="selectedPatient"/>
      </div>
    </div>
  </div>
</template>

<script>
import PatientList from '@/components/person/PatientList'
import PatientDetail from "@/components/person/PatientDetail";
import PatientEditor from "@/components/person/PatientEditor";
import patientService from '@/services/patient.service'

export default {
  data() {
    return {
      patients: [],
      selectedPatient: undefined,
      filter: '',
      modeEdit: false
    }
  },
  watch: {
    filter() {
      this.updatePatients()
    }
  },
  methods: {
    updatePatients() {
      patientService.getAllFiltered(this.filter).then(people => this.patients = people)
    }
  },
  mounted() {
    this.updatePatients()
  },
  components: {
    PatientList, PatientDetail, PatientEditor
  }
}
</script>
