<template>
  <div>
    <div class="card">
      <div class="card-header">
        <div>
          <icon icon="file-alt"
                size="2x"
                class="py-1 text-info align-middle"/>
          {{patient.firstName}} {{patient.lastName}} Patientenakte bearbeiten
        </div>
      </div>
      <div class="card-body">
        <validation-observer v-slot="{invalid}">
          <form @submit="$emit('save', patient)" v-if="editedPatient">
            <div class="form-group row">
              <div class="col-6">
                <label for="firstNameInput">Vorname</label>
                <validation-provider name="Vorname" rules="required" v-slot="{errors}">
                  <input
                      v-model="editedPatient.firstName"
                      id="firstNameInput"
                      class="form-control"
                      type="text"
                      autofocus>
                  <span class="form-error">{{ errors[0] }}</span>
                </validation-provider>
              </div>
              <div class="col-6">
                <label for="lastNameInput">Nachname</label>
                <validation-provider name="Nachname" rules="required" v-slot="{errors}">
                  <input
                      v-model="editedPatient.lastName"
                      id="lastNameInput"
                      class="form-control"
                      type="text"
                      autofocus>
                  <span class="form-error">{{ errors[0] }}</span>
                </validation-provider>
              </div>
            </div>
            <!-- KVNr -->
            <div class="form-group row">
              <div class="col-6">
                <label for="insurantNumberInput">KVNr</label>
                <validation-provider name="KVNr." rules="required" v-slot="{errors}">
                  <input
                      v-model="editedPatient.insurantNumber"
                      id="insurantNumberInput"
                      class="form-control"
                      type="text"
                      autofocus>
                  <span class="form-error">{{ errors[0] }}</span>
                </validation-provider>
              </div>
            </div>
            <!-- address -->
            <!-- street -->
            <div class="form-group row">
              <div class="col-4">
                <label for="streetInput">Stra√üe</label>
                <validation-provider name="Stra√üe" rules="required" v-slot="{errors}">
                  <input
                      v-model="editedPatient.street"
                      id="streetInput"
                      class="form-control"
                      type="text"
                      autofocus>
                  <span class="form-error">{{ errors[0] }}</span>
                </validation-provider>
              </div>
              <!-- streetnumber -->
              <div class="col-2">
                <label for="streetNumberInput">Nr.</label>
                <validation-provider name="Hausnummer" rules="required" v-slot="{errors}">
                  <input
                      v-model="editedPatient.streetNumber"
                      id="streetNumberInput"
                      class="form-control"
                      type="text"
                      autofocus>
                  <span class="form-error">{{ errors[0] }}</span>
                </validation-provider>
              </div>
              <!-- zip -->
              <div class="col-2">
                <label for="zipInput">Postleitzahl</label>
                <validation-provider name="Postleitzahl" rules="zip|required" v-slot="{errors}">
                  <input
                      v-model="editedPatient.zip"
                      id="zipInput"
                      class="form-control"
                      type="text"
                      autofocus>
                  <span class="form-error">{{ errors[0] }}</span>
                </validation-provider>
              </div>
              <!-- city -->
              <div class="col-4">
                <label for="cityInput">Stadt</label>
                <validation-provider name="Stadt" rules="required" v-slot="{errors}">
                  <input
                      v-model="editedPatient.city"
                      id="cityInput"
                      class="form-control"
                      type="text"
                      autofocus>
                  <span class="form-error">{{ errors[0] }}</span>
                </validation-provider>
              </div>
            </div>
            <!-- email -->
            <div class="form-group row">
              <div class="col-6">
                <label for="emailInput">Email</label>
                <validation-provider name="Email" rules="email" v-slot="{errors}">
                  <input
                      v-model="editedPatient.email"
                      id="emailInput"
                      class="form-control"
                      type="text"
                      autofocus>
                  <span class="form-error">{{ errors[0] }}</span>
                </validation-provider>
              </div>
            </div>
            <!-- phone/mobile -->
            <div class="form-group row">
              <!-- phone -->
              <div class="col-6">
                <label for="phoneInput">Telefon</label>
                <validation-provider mode="lazy" name="Telefon" rules="phone" v-slot="{errors}">
                  <input
                      v-model="editedPatient.phone"
                      id="phoneInput"
                      class="form-control"
                      type="text"
                      autofocus>
                  <span class="form-error">{{ errors[0] }}</span>
                </validation-provider>
              </div>
              <!-- mobile -->
              <div class="col-6">
                <label for="mobileInput">Mobil</label>
                <validation-provider name="Mobilnummer" rules="phone" v-slot="{errors}">
                  <input
                      v-model="editedPatient.mobile"
                      id="mobileInput"
                      class="form-control"
                      type="text"
                      autofocus>
                  <span class="form-error">{{ errors[0] }}</span>
                </validation-provider>
              </div>
            </div>
            <div class="d-block text-right">
              <button @click="$emit('cancel')"
                      class="btn btn-secondary">
                Abbrechen
              </button>
              <button type="submit"
                      class="btn btn-primary ml-2"
                      :disabled="invalid">
                Speichern
              </button>
            </div>
          </form>
        </validation-observer>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    patient: Object
  },
  data() {
    return {
      editedPatient: null
    }
  },
  mounted() {
    this.editedPatient = JSON.parse(JSON.stringify(this.patient))
  }
}
</script>
